import './App.css';
import React from "react";
import { HashRouter } from 'react-router-dom'
import { Cosmograph } from '@cosmograph/react';
const data = await import("./data.json")

function App() {
  return (
      <HashRouter>
        {
      <Cosmograph
          nodes={data.nodes}
          links={data.links}
          nodeColor={d => d.color}
          nodeSize={20}
          linkWidth={2}
      />
        }
      </HashRouter>
  );
}

export default App;
